import { Component } from '@angular/core';


@Component({

    selector:'app-form',
    template: `
<form (submit)="onSubmit(form)" #form ="ngForm">
<p>Email</p>
<input type="text" placeholder="email address" [(ngModel)]="email"   name="email" required />
<p>Password</p>

<input type="password" placeholder="password"  [(ngModel)]="password" name="password"   />
<br>

<button [disabled]="form.invalid" >Submit</button>  </form>
`
})

export class FormComponent
{
email='';
password='';
onSubmit(form)
    {
    console.log(form);
        console.log(form.invalid);
    }
}


