import { Component} from '@angular/core';
import {WeatherService} from './weather.service';

@Component ({
selector:'weather',
template:`

<input type="text" placeholder="Enter city here..." #txtCity  (keyup.Enter)="OnEnter(txtCity.value)" />

<h3><span>Tem: </span>{{tem}}  </h3>

`,
   }) 
    export class WeatherComponent
    {
        
        tem=10;
        constructor(private weatherService:WeatherService){}
        
        OnEnter(city:string)
        {
           this.weatherService.getCity(city)
            .then(city=>this.tem=city)
            .catch(err=>console.log(err));
            
           // console.log(this.weatherService.getCity()
           // .then(city=>city.message));
            
       // this.tem=word;
        }

}




