import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {FormsModule } from '@angular/forms'

import { HttpModule } from '@angular/http';
import { AppComponent } from './app.component';
import { PeopleComponent } from './components/people.component';
import { ServiceComponent } from 
'./components/service.component';


import { WeatherComponent } from 
'./components/weather.component';

import { WeatherService } from 
'./components/weather.service';

import {ServiceService} from './components/service.service';


import {FormComponent} from './components/form.component';


@NgModule({
    
  declarations: [
    AppComponent,
    PeopleComponent,
    ServiceComponent,
      WeatherComponent,
      FormComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
      FormsModule
  ],
  providers: [ServiceService,WeatherService],
  bootstrap: [AppComponent]
})
export class AppModule { }



