import {Component,OnInit } from '@angular/core';
import {ServiceService} from './service.service';

@Component({
selector:'service',
    template:'<h1>{{name}}</h1>',

})


export class ServiceComponent {
    name="service";
    ip:string;
    constructor (private ipService: ServiceService)
    {

        
        
    }
    
    ngOnInit():void{
    this.ipService.getIP().then(ip1=>this.setName(ip1));
    console.log( this.ipService.getIP());
    }
    setName(name)
    {
    this.name=name + " sss";
    }
}


